.cell {
    width: 256px;
    -webkit-transition: width 500ms ease-out;
    -o-transition: width 500ms ease-out;
    transition: width 500ms ease-out;
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
    cursor: pointer;
}

.enlarged1, .enlarged2 {
    width: 512px;
    grid-column: span 2;
    grid-row: span 2;
}


